'use client'
import styles from "../../page.module.css";
import Form from 'react-bootstrap/Form';
import EntryBox from "../../_components/entrybox";
import Submit from "../../_components/submit";
import Select from "../../_components/select";
import { useEffect, useReducer } from "react";
import CheckBox from "../../_components/checkbox";
import { fetch } from "../../_scripts/communications";
import backgroundImage from "../../../public/Green.png"
import FormTitle from "@/app/_components/formtitle";
import Feedback from "@/app/_components/feedback";

let submitObject = {};

const initState = {
    'LCS Account Number': {
        value: ''
    },
    'Total Purchased':{
        value:1
    },
    'Submit':{
        Color:'primary',
        Text:'Submit'
    },
    'Purchase Source':{
        options: [],
        value:0
    },
    'Feedback':{
        color:'success',
        code:'',
        title:'',
        text:'',
        hidden: true
    }
};


const reducer = (state, action)=>{

    let newState = {...state};

    switch (action.type){

        case 'update-Value':{

            newState[action.label].value = action.value;

            newState.Submit.Color = 'primary';
            newState.Submit.Text = 'Submit'

            return newState

            break
        }
        case 'submit-Clicked':{

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }
        case 'submit-Error':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }
        case 'submit-Success':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;

        }
        case 'mark-Checked':{

            newState[action.label].isChecked = action.value;

            return newState

            break;

        }
        case 'load-Options':{

            //console.dir(action.data);

            let sources = action.data.purchaseSources;

            newState['Purchase Source'] = sources;

            newState['Purchase Source'].options = sources.map(e=>{return{ID:e.ID,Name:`${e.Name}`}});
            
            newState['Purchase Source'].value = newState['Purchase Source'].options[0].ID;

            return newState

            break;
        }
        case 'load-Feedback':{

            newState['Feedback'].hidden = false;
            newState['Feedback'].color = action.color;
            newState['Feedback'].code = action.code;
            newState['Feedback'].title = action.title;
            newState['Feedback'].text = action.text;

            return newState;

            break;

        }

    }
};

export default function AddPurchase(){


    const [state, dispatch] = useReducer(reducer,initState);

    useEffect(function(){

        const loadDropListOptions = async()=>{

            let options = await fetch({method:'GET',endpoint:'GetPurchaseSources'});

            if(options.didError){

                //Do stuff

                return

            }

            dispatch({type:'load-Options',data:options.data})

        }

        loadDropListOptions();
    
        return

        },[]
    );

        
        submitObject = {
            LSManager_Account:state['LCS Account Number'].value,
            Amount:state['Total Purchased'].value,
            Purchase_Source:state['Purchase Source'].value,
        }
        

    return (

        <main className={styles.main} style={{backgroundImage: `url(${backgroundImage.src})`}}>
            
          <Form>
            <FormTitle text='Add Purchase'></FormTitle>
            <EntryBox
                label='LCS Account Number'
                type = 'numeric'
                reminder ='Please Enter for LSManager'
                placeholder = ''
                value = {state['LCS Account Number'].value}
                dispatch={dispatch}
            ></EntryBox>
            <EntryBox
                label='Total Purchased'
                type = 'numeric'
                reminder =''
                placeholder = ''
                value = {state['Total Purchased'].value}
                dispatch={dispatch}
            ></EntryBox>
            <Select
                options={state['Purchase Source'].options}
                label='Purchase Source'
                value={state['Purchase Source'].value}
                dispatch={dispatch}
            ></Select>
            <Submit 
                method="GET"
                endpoint="AddPurchase"
                dispatch={dispatch}
                data={submitObject}
                color={state.Submit.Color}
                text={state.Submit.Text}
            ></Submit>
            <Feedback
                color={state['Feedback'].color}
                code={state['Feedback'].code}
                title={state['Feedback'].title}
                text={state['Feedback'].text}
                hidden={state['Feedback'].hidden}
            ></Feedback>
          </Form>
        </main>
    );
}